import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import {MyApp} from "./app.component";
import {LoginPage} from "../pages/login/login";
import {LoginAuthPage} from "../pages/login/loginAuth/loginAuth";
import {MessageAuthPage} from "../pages/login/messageAuth/messageAuth";
import {ForgetPwdPage} from "../pages/login/forgetPwd/forgetPwd";
import {TabsPage} from "../pages/tabs/tabs";
import {ResetPwdPage} from "../pages/login/resetPwd/resetPwd";





@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    LoginAuthPage,
    MessageAuthPage,
    ForgetPwdPage,
    ResetPwdPage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      backButtonText:'返回',
      tabsHideOnSubPages:true
    }),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    LoginAuthPage,
    MessageAuthPage,
    ForgetPwdPage,
    ResetPwdPage,
    TabsPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},

  ]
})
export class AppModule {}



