import {Component} from '@angular/core';
import {NavController, NavParams} from 'ionic-angular';
import {ResetPwdPage} from '../resetPwd/resetPwd';
import {TabsPage} from "../../tabs/tabs";


@Component({
  selector: 'page-messageAuth',
  templateUrl: 'messageAuth.html',
  providers: []
})
export class MessageAuthPage {
  countDown: number = 60;
  timerID: number;
  desc: string = '已发送验证码至';
  btnText: string = '确认';
  showBackBtn = true;
  // 严重信息
  authMessage: string = '';

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    console.log('navParams', this.navParams);
    if (this.navParams.get('from') === 'forgetPwd') {
      this.desc = '已发送验证码至密保手机号'
    }
    if (this.navParams.get('from') === 'editTel') {
      this.desc = '已发送验证码至绑定手机';
      this.btnText = '下一步';
      this.showBackBtn = false;
    }
  }

  ionViewDidEnter() {
    this.countDownFn();
  }

  restartSendMessage() {
    this.countDown = 60;
    this.countDownFn();
  }

  countDownFn() {
    clearInterval(this.timerID);
    this.timerID = setInterval(() => {
      if (this.countDown > 1) {
        this.countDown = this.countDown - 1;
      }
    }, 1000)
  }

  sureBtn() {


    if (this.navParams.get('from') === 'forgetPwd') {
      // 验证号码是否正确（忘记密码）
      this.navCtrl.push(ResetPwdPage)

    } else if (this.navParams.get('from') === 'login') {
      // 登录
      this.navCtrl.setRoot(TabsPage);
    } else if (this.navParams.get('from') === 'editTel') {
      // 验证号码是否正确（修改绑定手机号码）
      // ajax....
      this.navCtrl.push('edit-tel-second')
    }
  }

}



