import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { MessageAuthPage } from '../messageAuth/messageAuth';
import { AlertController } from 'ionic-angular';

@Component({
  selector: 'page-forgetPwd',
  templateUrl: 'forgetPwd.html'
})
export class ForgetPwdPage {
  account:string ='';
  constructor(public navCtrl: NavController,public alertCtrl: AlertController) {

  }
  next(){
    if(this.account.trim().length>0){
      this.navCtrl.push(MessageAuthPage,{from:'forgetPwd'});
    }else{
      let alert = this.alertCtrl.create({
        title: '请输入账号',
        buttons: ['确定']
      });
      alert.present();
    }
    
  }

}



