import { Component } from '@angular/core';
import {ApplicationRecordPage} from "../application-record/application-record";
import {AssociationsCustomersPage} from "../associations-customers/associations-customers";
import {MyPage} from "../my/my";
import {Events, NavController} from "ionic-angular";
import {LoginPage} from "../login/login";

@Component({
  selector: 'page-tabs',
  templateUrl: './tabs.html'
})
export class TabsPage {


  tab1Root = 'application-record';
  tab2Root = 'associations-customers';
  tab3Root = 'my';
 // tab2Root = AssociationsCustomersPage;
 // tab3Root = MyPage;
 // Associations customers
  constructor(private events: Events,
              private nav: NavController) {

  }

  ionViewDidLoad() {
    this.listenEvents();
    // console.log('界面创建');
  }

  ionViewWillUnload() {
    this.events.unsubscribe('toLogin');
    // console.log('界面销毁');
  }

  listenEvents() {
    this.events.subscribe('toLogin', () => {
      this.nav.setRoot(LoginPage);
    });
  }
}





